.blocFooter {
  margin-top: 5%;
}
.bg-dark {
  background-color: #f5a624 !important;
}
.title {
  font-size: xx-large;
}
.mr-auto {
  display: block;
  margin-left: 10%;
}
.mr-auto2 {
  display: block;
  margin-right: 20%;
  font-size: 20px;
  color: #4a4a4a;
}
.fab {
  color: #4a4a4a;
  margin: 3px;
  font-size: 25px;
  margin-right: 10px;
}
.fab fa-facebook-square {
  margin-right: 5px;
}
.social {
  margin-right: 20px;
}
.icons {
  margin-left: 20px;
  font-size: 35px;
}
.navbar-dark .navbar-brand {
  color: #4a4a4a;
}
.navbar-expand .navbar-nav .nav-link {
  color: #4a4a4a !important;
}
@media only screen and (max-width: 600px) {
  .footerOrdi {
    display: none;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) and (max-width: 768px) {
  .desktop {
    display: none;
  }

  .mr-auto2 {
    margin-right: 12%;
  }
}
