/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .desktop {
    display: none;
  }
  .ipad {
    display: none;
    margin-bottom: 5%;
  }
  .colContact {
    margin-bottom: 20% !important;
  }
  .backgroundMobile {
    background-color: #f5ce24;
    width: 100%;
  }

  .backgroundButton {
    background-color: #f5ce24;
  }

  .restaurateur {
    width: 100%;
  }

  .serveur {
    width: 100%;
    margin-top: 3%;
    margin-bottom: 3%;
  }
  /*row services mobile*/
  .servicesMobiles {
    text-align: center !important;
    margin-top: -20%;
    padding-left: 10%;
    padding-right: 10%;
  }
}

